nav {
  min-height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-top:10px;
  h1 {
    color: #C32B37;
    margin-top:10px;
  }
  button {
    background: rgb(218, 218, 218);
    border: none;
    cursor: pointer;
    // border: 2px solid rgb(243, 243, 243);
    padding: 0.5rem;
    transition: all 0.3s ease-out;
    outline: none;
    &:hover {
      // background: rgb(65, 65, 65);
      color: rgb(92, 92, 92);
    }
    svg { 
      // margin-left: 5px;
      min-height: 30px;
      min-width: 30px; 
      margin-top:10px;
    } 
  }
}
@media screen and (max-width: 768px) {
  nav {
    button {
      z-index: 10;
    }
  }
}
